<template>
    <div>
        <div style="background:#173E62;" class="pt-6 pb-5">
            <div class="container">
                <div class="row justify-content-center">
                <div class="col-12">
                </div>
                  <div class="col-md-6">
                    <div class="p-2">
                        <img :src="datanya[0].gambar" class="img-thumbnail" style="width:100%;" v-viewer alt="">
                        <br>
                        <div class="row justify-content-center">
                            <div class="col-3" v-for="(item, index) in gambar" :key="index+'gambar'">
                                <img :src="item.gambar" class="img-thumbnail" style="width:100%;height:80px;" v-viewer alt="">
                            </div>
                        </div>
                    </div>
                  </div>
                  <div class="col-md-6">
                    <p class="font-semibold text-lg text-white capitalize p-4">{{datanya[0].title}}</p>
                    <div class="p-4 shadow rounded-lg bg-white">
                    <div class="p-4 shadow rounded-lg bg-white">
                        <p class="text-2xl text-right font-italic font-bold">Rp. {{numberWithCommas(datanya[0].harga)}}</p>
                        <hr class="mt-1 mb-1">
                        <div class="row">
                            <div class="col-7">
                                <p class="text-lg font-semibold "><span class="typcn typcn-info"></span> Detail</p>
                                <p class=" font-semibold text-xs mt-5" style="white-space:pre;" v-html="datanya[0].detail"></p>
                            </div>
                            <div class="col-5">
                                <p class="text-lg font-semibold "><span class="typcn typcn-home"></span> Fasilitas</p>
                                <p class=" font-semibold text-xs mt-5" style="white-space:pre;" v-html="datanya[0].fasilitas"></p>
                            </div>
                        </div>
                        <br>
                        <hr class="style4">
                        <div class="row">
                            <div class="col-6">
                                <a v-if="datanya[0].id360" :href="`/#/office/360view?idgambar=${datanya[0].id360.length>0?datanya[0].id360:'6'}`" target="__blank">
                                    <button style="font-size:12px;" type="button" class="font-bold btn btn-sm btn-style12  btn-block text-xs text-center">
                                        <img src="https://upload.egov.balangankab.go.id/svg/arrow-loop.svg" 
                                        class="d-inline" style="height:15px;width:15px;" alt="">
                                        <br>
                                        360 Tour
                                    </button>
                                </a>
                            </div>
                            <div class="col-6">
                                <a :href="datanya[0].dollhouse" v-if="datanya[0].dollhouse" target="__blank">
                                    <button style="font-size:12px;" type="button" class="font-bold btn btn-sm btn-style12  btn-block text-xs text-center">
                                        <img src="https://upload.egov.balangankab.go.id/svg/globe.svg" 
                                        class="d-inline" style="height:15px;width:15px;" alt="">
                                        <br>
                                        3d Dollhouse
                                    </button>
                                </a>
                            </div>
                        </div>
                        <hr class="style4">
                        <button type="button" class="btn btn-sm btn-style4 ml-1" style="font-size:9px;" v-for="(item, index) in datanya[0].tag.split(',')" :key="index+'tag'">{{item}}</button>
                    </div>
                    </div>
                  </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
    layout:'app_landing_estate2',
    data(){
        return{
            datanya:[],
            gambar:[]
        }
    },
    mounted() {
        this.getData();
    },
    methods: {
        numberWithCommas(x) {
            return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
        getData(){
            sdb.collection("app_gis_ruangan",false).doc().select(`select * from app_gis_ruangan where id='${this.$route.query.id}'`).then(res => {
                console.log('ruangan',res)
                this.datanya=res;
                this.$forceUpdate();
            });
            sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${this.$route.query.id}'`).then(res => {
                this.gambar=res;
                this.$forceUpdate();
            });
            
        },
        test(){
            alert('y')
        }
    },
}
</script>